  animation: rotation 1s linear infinite;
  display: flex;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.loader--hidden {
  opacity: 0;
  visibility: hidden;
}

.loader:after {
  content: "";
  box-sizing: border-box;
  width: 80px;
  height: 80px;
  border-bottom: 6px solid rgba(0, 0, 0, 0);
  border-left: 6px solid #ff6b08;
  border-radius: 50%;
  animation: rotation .5s linear infinite reverse;
  position: absolute;
  top: 0;
  left: 0;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

